<script lang="ts" context="module">import marked from "marked";
import hljs from "highlight.js";
import DOMPurify from "dompurify";
const getDOM = (instraction) => {
    const mainNote = document.getElementById("main_note");
    if (!mainNote)
        throw ("mainNote is null");
    const previewNote = document.getElementById("preview_note");
    if (!previewNote)
        throw ("previewNote is null");
    const returnedObject = { mainNote: mainNote, previewNote: previewNote };
    const returnedArray = [mainNote, previewNote];
    if (instraction === "object")
        return returnedObject;
    if (instraction === "array")
        return returnedArray;
};
// marked.jsとDOMPurifyで簡単なmarkdownパース
export const parseElement = () => {
    const DOMObj = getDOM("object");
    marked.setOptions({
        langPrefix: "",
        highlight: (code, lang) => {
            return hljs.highlightAuto(code, [lang]).value;
        },
    });
    const originalText = DOMObj.mainNote.value;
    const unsanitizedDirtyElement = marked(originalText);
    const sanitizedElement = DOMPurify.sanitize(unsanitizedDirtyElement);
    DOMObj.previewNote.innerHTML = sanitizedElement;
};
export const changeAppearance = (clickedNum) => {
    const DOMObj = getDOM("array");
    const clickedElement = DOMObj[clickedNum];
    const oppositeElement = DOMObj[1 - clickedNum];
    clickedElement.style.zIndex = "3";
    oppositeElement.style.zIndex = "2";
};
</script>

<textarea class="main-note center-content" id="main_note"></textarea>
<div class="preview-note center-content" id="preview_note"></div>

